@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

#root {
  font-size: 62.5%;
}

body {
  background-color: #f7f3e9;
  color: #4f4f4f;
  margin: 0;
  overflow: hidden;
  padding: 1rem 3rem;
  font-family: 'Roboto', sans-serif;
  height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.wrapper {
  margin: auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.shape {
  position: fixed;
  width: 110vw;
  height: 80vh;
  z-index: -10;
}

header img {
  cursor: pointer;
  width: 8rem;
  padding-top: 1rem;
}

main {
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
}

.upper-row,
.lower-row {
  display: flex;
}

.headline {
  margin-top: -10rem;
}

h1 {
  font-size: 8rem;
  font-weight: 700;
  margin: 0;
}

.main-copy {
  align-items: flex-start;
  margin-top: -8rem;
  display: flex;
  flex-direction: column;
}

.copy {
  max-width: 27rem;
  font-size: 1.2rem;
  margin-bottom: 2rem;
  z-index: 2;
}

.copy p {
  line-height: 1.5;
  margin: 9px 0;
}

.copy p span {
  font-weight: 700;
}

.links {
  display: flex;
  font-size: 0.8rem;
  justify-content: space-between;
  min-width: 95%;
}

.links a {
  color: #4f4f4f;
  font-weight: 500;
  text-decoration: none;
}

.links a:hover {
  color: #121111;
}

@media screen and (max-width: 700px) {
  body {
    padding: 0.5rem;
  }

  header {
    padding: 0 1rem;
  }

  .shape {
    display: none;
  }

  header img {
    width: 6rem;
  }

  .headline-wrapper {
    align-self: center;
    margin: auto;
  }

  .headline {
    margin-top: -11rem;
  }

  h1 {
    font-size: 3.5rem;
    font-weight: 700;
  }

  .main-copy {
    align-items: flex-start;
    margin-top: -6rem;
    padding: 0 2rem;
  }

  .copy p {
    line-height: 1.4;
    font-size: 1.4rem;
  }

  .copy p span {
    font-weight: 700;
  }

  .links {
    display: flex;
    font-size: 0.8rem;
    justify-content: space-between;
    width: 95%;
  }
}

@media screen and (max-width: 380px) {
  .headline {
    margin-top: -12rem;
  }

  .main-copy {
    margin-top: -5rem;
  }

  .main-copy p {
    font-size: 1.25rem;
  }
}
